.App {
  text-align: center;
  background-color:black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.results-list {
  list-style-type: none;

}

.input-field {
  background-color: #ffff;
  border-radius: 25px;
  border:2px solid #282c34;

}

.input-button {
  background-color: #282c34;
  color: #ffff;
  border-radius: 25px;
  
}

a.artist-link:hover {
  font-size: 25px;

}

.artist-link {
  color:#ffff;
  text-decoration: none;
}


